import React, { useState } from "react";
import { Collapse, Navbar, NavbarToggler, Nav, NavItem } from "reactstrap";
import "./menu.css";
import { Link } from "react-scroll";

const Menu = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  return (
    <Navbar className="navbar" expand="md" fixed="top">
      <Link to="root" spy={true} smooth={true} className="logo">
        Daniel Medina Cea
      </Link>
      <NavbarToggler onClick={toggle} className="botonMenuDesplegable" />
      <Collapse isOpen={isOpen} navbar>
        <Nav navbar>
          <NavItem>
            <Link to="educacion" smooth={true} className="link" offset={-50}>
              Educaci√≥n
            </Link>
          </NavItem>
          <NavItem>
            <Link
              to="competencias-digitales"
              spy={true}
              smooth={true}
              className="link"
            >
              Competencias
            </Link>
          </NavItem>
          <NavItem>
            <Link to="proyectos" spy={true} smooth={true} className="link">
              Proyectos
            </Link>
          </NavItem>
          <NavItem>
            <Link to="contacto" spy={true} smooth={true} className="link">
              Contacto
            </Link>
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  );
};

export default Menu;
