import React from "react";
import "./home.css";
import Typical from "react-typical";

const steps = [
  "BIENVENIDO/A üëã",
  1500,
  "Soy Desarrollador Web",
  1000,
  "Soy Desarrollador Full Stack üëå",
  1000,
  "Soy Desarrollador en React",
  1000,
  "MERN Developer",
  1000,
];

const Home = () => {
  return (
    <section className="hero-wrap js-fullheight" id="hero">
      <div className="container">
        <div className="row no-gutters slider-text js-fullheight justify-content-center align-items-center">
          <div
            className="col-lg-8 col-md-6 d-flex align-items-center home-text"
            data-aos="fade-up"
            data-aos-anchor-placement="top-bottom"
          >
            <div className="text text-center">
              <h1 className="nombre">Daniel</h1>
              <h2>
                <span id="typed-strings">
                  <Typical
                    wrapper="span"
                    steps={steps}
                    loop={Infinity}
                    className="caca info"
                  />
                </span>
              </h2>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Home;
