import React from "react";
import "./proyectos.css";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  Button,
} from "reactstrap";
import toDoList from "./to-do-list.PNG";
const Proyectos = () => {
  return (
    <div className="row fila" id="proyectos" data-aos="fade-up-right">
      <div className="w-100 mt-3 text-center">
        <h1 className="titulo-proyectos">Proyectos</h1>
      </div>
      <Card className="col-6 mx-auto">
        <CardImg top width="100%" src={toDoList} alt="Card image cap" />
        <CardBody>
          <CardTitle tag="h5">TO-DO LIST</CardTitle>
          <CardSubtitle tag="h6" className="mb-2 text-muted">
            Gestor de Tareas
          </CardSubtitle>
          <CardText>
            Peque√±o gestor de tareas hecho en React usando como base de datos el
            local storage
          </CardText>
          <Button className="abrirProyecto">
            <a
              href="https://danielmedinacea.github.io/To-do-List/"
              target="_blank"
              rel="noreferrer"
              className="enlaceProyecto"
            >
              Abrir
            </a>{" "}
          </Button>
        </CardBody>
      </Card>
    </div>
  );
};

export default Proyectos;
